<template>
    <div>
 <v-navigation-drawer color="transparent"
    floating
    
      v-model="drawer"
      app>
      
    <div v-if="currentUser">

      
        <v-row
            align="end"
            class="fill-height"
        >
            <v-col
            align-self="start"
            class="pa-0"
            cols="12"
            ><br>
            <v-avatar
                id="avataras"
                class="profile mr-4"
                color="grey"
                size="90"
                tile 
            >
                <v-img src="http://combineoverwiki.net/images/thumb/6/62/Gaben.jpg/250px-Gaben.jpg"></v-img>
            </v-avatar>
            </v-col>
            <v-col class="py-0">
            <v-list-item
                color="rgba(0, 0, 0, .4)"
                dark
            >
                <v-list-item-content>
                <v-list-item-title class="description pa-2 text-center">Welcome back <br><span id="userName">{{ currentUser.email }}</span></v-list-item-title>
                </v-list-item-content>
            </v-list-item>
            </v-col>
        </v-row>
 

     
    </div>
      <ul>
        <router-link class="li" tag="li" to="/" > <v-icon color="white">home</v-icon> Home</router-link>
        <router-link class="li" tag="li" to="/menu" > <v-icon color="orange">mdi-shopify</v-icon> Store</router-link>
        <router-link class="li" tag="li" to="/about"> <v-icon color="white">info</v-icon> About</router-link> <br>
        <router-link class="li" tag="li" to="/login"> <v-icon color="white">mdi-login</v-icon> Login</router-link>
        <router-link class="li" tag="li" to="/register"> <v-icon color="white">mdi-account-plus</v-icon> Register</router-link>
        <router-link tag="li" v-if="currentUser" to="/orders" class="li"> <v-icon color="orange">mdi-shopping</v-icon> Orders</router-link>
        <router-link v-if="currentUser" class="li" tag="li" to="/admin"> <v-icon color="red">mdi-account-lock</v-icon> Admin Panel</router-link>
          <v-btn tag="li" v-if="currentUser" class="text-center font-weight-black" color="red" text="white" @click.prevent="signOut()">
            <v-icon>mdi-logout</v-icon>Logout 
                 </v-btn>
      </ul>
    </v-navigation-drawer>

    <v-app-bar
      app>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title class="headline">
         <a  href="/"> <img id="logo" src="../../src/assets/Logo_Simas.png" height="60"><img></a>
        </v-toolbar-title>
    </v-app-bar>

    </div>
</template>

<script>
import { db } from '../../firebase'
import firebase from 'firebase'
import 'firebase/firestore'
import store from '../store.js'



firebase.auth().onAuthStateChanged(function(user) {
 if (user) {
     store.dispatch('setUser', user)
 } else {
    store.dispatch('setUser', null)
 }
});

    export default {
          data: () => ({
              drawer: null,
    }),
            methods: {
            signIn() {
               firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(() => {
                this.$router.replace('/admin')
               })
           .catch(function(error){
               var errorCode = error.errorCode
               var errorMessage = error.message;
               if (errorCode === 'auth/wrong-password') {
                   alert ("Wrong Password")
               } else {
                   alert (errorMessage)
               }
               console.log(error)
           })
        },

            signOut() {
                    firebase.auth().signOut().then(() => {
                        alert ('Logged Out');
                        this.$router.replace('/')
                    }).catch(error => {
                        alert(error)
                    })
                    }
                },
            computed: {
        currentUser() {
            return this.$store.getters.currentUser
        }
    }
    }
</script>

<style lang="scss" scoped>
    
    #avataras{
        margin-left: 95px;
    }
    .profile{
        border-radius: 50%;
    }

    nav ul {
        padding: 0;
        margin-top: 20px;
        text-decoration: none;
    }

    nav li {
        //scss doesn't work 
        color: rgb(255, 255, 255);
        margin-left: 15%;  
        padding: 5px 20px;
        list-style-type: none;
        cursor: pointer;
    }

    nav li i {
        margin-right: 10px;
    }

    nav li:last-child {
        position: absolute;
        bottom: 40px;
        color: rgb(255, 255, 255);
    }
    
     .li {
   padding: 10px;
  }
  .li:hover {
    background: rgba(255, 255, 255, 0.171);
  }
  .li:active {
    background: rgba(253, 253, 253, 0.514);
  }
  #userName{
      font-size: 20px;
  }
  .headline{
      margin-left: 41%;
  }
</style>