<template>
  <div class="container offset-3">
    <div class="row justify-content-center">
      <div class="col-md-6">
        <div class="card">
            <br>
            <br>
            <br>
          <h1 class="text-center font-weight-black">Register New User</h1>
          <br>
          <div id="info" class="card-body">
            <div v-if="error" class="alert alert-danger">{{error}}</div>
            <form action="#" @submit.prevent="submit">
              <div class="form-group row">
                <label for="name" class="col-md-4 col-form-label text-md-right">Name</label>

                <div class="col-md-6">
                  <input
                    id="name"
                    type="name"
                    class="form-control"
                    name="name"
                    value
                    required
                    autofocus
                    v-model="form.name"
                  />
                </div>
              </div>

              <div class="form-group row">
                <label for="email" class="col-md-4 col-form-label text-md-right">Email</label>

                <div class="col-md-6">
                  <input
                    id="email"
                    type="email"
                    class="form-control"
                    name="email"
                    value
                    required
                    autofocus
                    v-model="form.email"
                  />
                </div>
              </div>

              <div class="form-group row">
                <label for="password" class="col-md-4 col-form-label text-md-right">Password</label>

                <div class="col-md-6">
                  <input
                    id="password"
                    type="password"
                    class="form-control"
                    name="password"
                    required
                    v-model="form.password"
                  />
                </div>
              </div>

              <div class="form-group row mb-0">
                <div class="col-md-8 offset-md-4">
                  <button type="submit" class="btn btn-primary mr-5 text-center font-weight-black" text="white" color="#241575"> <v-icon>mdi-login</v-icon>Register</button>
                    <v-btn type="submit" class="mr-5 text-center font-weight-black" color="red" text="white" to="/">
                      <v-icon>mdi-login</v-icon>Cancel
                 </v-btn>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import firebase from "firebase";

export default {
  data() {
    return {
      form: {
        name: "",
        email: "",
        password: ""
      },
      error: null
    };
  },
  methods: {
    submit() {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.form.email, this.form.password)
        .then(data => {
          data.user
            .updateProfile({
              displayName: this.form.name
            })
            .then(() => {});
        })
        .catch(err => {
          this.error = err.message;
        });
    }
  }
};
</script>

<style lang="scss" scoped>

    *{
        color: rgb(0, 0, 0);
    }
     h1{
         color: white;
         border: 5px solid #fff;
         padding: 10px;
         font-size: 21px;
     }
 #name, #email, #password{
     background-color: rgba(255, 255, 255, 0.335);
     border: darkgrey 1px solid;
 }
 form{
     padding: 10px;
 }

 #info{
     background-color: none;
     opacity: 0.9;
 }
</style>